import { Injectable } from "@angular/core";
import { CanActivate, CanActivateChild, Router } from "@angular/router";

import { Observable } from "rxjs";

import { Store } from "@ngrx/store";

import * as fromRoot from "../../reducers";

import { User } from "../../models";

import { AuthService } from "./auth.service";

export abstract class BaseAuthGuard implements CanActivate, CanActivateChild {
	protected abstract handleRedirect(loggedIn: boolean): void;
	protected abstract activate(loggedIn: boolean): boolean;

	constructor(
		protected authService: AuthService,
		protected router: Router,
		protected store: Store<fromRoot.State>
	) { }

	canActivate(): Observable<boolean> {
		if (!this.authService.initted) {
			return this.authService
				.verify()
				.take(1)
				.flatMap(() => this.store.select(fromRoot.getLoggedIn))
				.do((loggedIn: boolean) => this.handleRedirect(loggedIn))
				.map((loggedIn: boolean) => this.activate(loggedIn));
		}

		return this.store
			.select(fromRoot.getLoggedIn)
			.do((loggedIn: boolean) => this.handleRedirect(loggedIn))
			.map((loggedIn: boolean) => this.activate(loggedIn));
	}

	canActivateChild(): Observable<boolean> {
		return this.canActivate();
	}
}



