import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { RouterModule } from "@angular/router";

import { ComponentsModule } from "../components";

import { InteractionDirectivesModule } from "../directives/interactions";

import { UiServicesModule } from "../services/ui";

import { BookScannerComponent } from "./book-scanner";
import { BookListPageComponent } from "./book-list-page";
import { HomepageComponent } from "./homepage";
import { LoginPageComponent } from "./login-page";
import { NewBookPageComponent } from "./new-book-page";

const DIRECTIVES = [
	BookListPageComponent,
	BookScannerComponent,
	HomepageComponent,
	LoginPageComponent,
	NewBookPageComponent
];

@NgModule({
	imports: [
		CommonModule,
		ComponentsModule,
		FormsModule,
		InteractionDirectivesModule,
		RouterModule,
		UiServicesModule
	],
	declarations: DIRECTIVES,
	exports: DIRECTIVES
})
export class ContainersModule { }



