import { Injectable } from "@angular/core";
import { Effect, Actions, toPayload } from "@ngrx/effects";
import { Action } from "@ngrx/store";

import { Observable } from "rxjs";

import { Book, JsonBook, Paragraph } from "../models";
import { BooksApi, BookStorageService, ParagraphParserService } from "../services";

import * as books from "../actions/books";

@Injectable()
export class BookEffects {
	@Effect()
	getById$: Observable<Action> =
		(this.actions$.ofType(books.GET_BY_ID) as Observable<Action>)
		.map(toPayload)
		.switchMap((id) => {
			if (id == null) {
				return Observable.empty();
			}

			if (this.bookStorage.isStored(id)) {
				const jsonBook: JsonBook = this.bookStorage.getById(id);
				return Observable.of(new books.LoadAction(this.fromJson(jsonBook)));
			}

			const nextGetById$ =
				(this.actions$.ofType(books.GET_BY_ID) as Observable<Action>).skip(1);

			return this.booksApi
				.getById(id)
				.takeUntil(nextGetById$)
				.map((book: Book) => new books.LoadAction(book));
		});

	@Effect()
	write$: Observable<Action> =
		(this.actions$.ofType(books.WRITE) as Observable<Action>)
		.map(toPayload)
		.switchMap((jsonBook) => {
			if (jsonBook == null) {
				return Observable.empty();
			}

			const nextWrite$ =
				(this.actions$.ofType(books.WRITE) as Observable<Action>).skip(1);

			return this.booksApi
				.createFromJsonBook(jsonBook)
				.takeUntil(nextWrite$)
				.map(() => new books.WriteFinishAction());
		});

	@Effect()
	storeLocally$: Observable<Action> =
		(this.actions$.ofType(books.STORE_LOCALLY) as Observable<Action>)
		.map(toPayload)
		.do((payload) => {
			this.bookStorage.set(payload.json);
			return Observable.empty();
		});

	constructor(
		private actions$: Actions,
		private booksApi: BooksApi,
		private bookStorage: BookStorageService,
		private paragraphParserService: ParagraphParserService
	) { }


	fromJson(json: JsonBook): Book {
		const content: Paragraph[] = this.paragraphParserService.parse(json.body);
		const length: number = content.reduce((sum: number, paragraph: Paragraph) => {
				sum += paragraph.length;
				return sum;
			}, 0);
		return {
			id: json.id,
			title: json.title,
			author: json.author,
			content,
			length
		};
	}
}



