import { Component, ElementRef, HostBinding, Input } from "@angular/core";

import * as fromRoot from "../../reducers";
import { Store } from "@ngrx/store";
import { Observable, Subscription } from "rxjs";

import * as reader from "../../actions/reader";

import { BookPosition, Paragraph } from "../../models";

@Component({
	selector: "app-scanner-paragraph",
	templateUrl: "./scanner-paragraph.template.html"
})
export class ScannerParagraphComponent {
	@Input()
	paragraph: Paragraph;

	@Input()
	currentPosition: BookPosition;

	@Input()
	id: number;

	@Input()
	percent: number;

	@HostBinding("class.paragraph")
	hostClass: boolean = true;


	private documentElement = document.documentElement;
	private documentBody = document.body;

	private hasScrolled: boolean = false;
	@Input()
	get selected(): boolean {
		return this._selected;
	}
	set selected(val: boolean) {
		if (val === this._selected) {
			return;
		}

		if (val) {
			this.hasScrolled = false;
			this.scrollIntoView();
		}
		this._selected = val;
	}
	private _selected: boolean;

	private playing: boolean;

	constructor(
		private elementRef: ElementRef,
		private store: Store<fromRoot.State>
	) {
		this.store.select(fromRoot.getPlay).subscribe((val) => this.playing = val);
	}

	/**
	 * Only runs when selected is INITIALLY set to true
	 * Sets scroll depth so that selected paragraph is in view
	 *
	 * @private
	 * @memberof ScannerParagraphComponent
	 */
	private scrollIntoView(): void {
		const htmlElement: HTMLElement = this.elementRef.nativeElement;
		const overflowBottom = (htmlElement.offsetTop + htmlElement.getBoundingClientRect().height) - (window.scrollY + window.innerHeight);
		const overflowTop = window.scrollY - htmlElement.offsetTop;
		const restartAfterScroll = this.playing;

		if (overflowBottom > 0 || overflowTop > 0) {
			this.store.dispatch(new reader.StopPlayAction());
			this.smoothScroll(overflowTop, restartAfterScroll);
			return;
		}
	}

	private smoothScroll(pixels: number, restartAfterScroll?: boolean): void {
		if (this.hasScrolled) {
			return;
		}

		this.documentElement.scrollTop -= pixels * 0.1;
		this.documentBody.scrollTop -= pixels * 0.1;

		if (Math.abs(pixels) < 5) {
			this.hasScrolled = true;
			return restartAfterScroll ? this.store.dispatch(new reader.StartPlayAction()) : undefined;
		}

		requestAnimationFrame(() => this.smoothScroll(pixels * 0.9, restartAfterScroll));
	}
}



