import { CommonModule } from "@angular/common";
import { Directive, NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";

import { InteractionDirectivesModule } from "../directives/interactions";

import { HeaderComponent } from "./header";
import { ScannerParagraphComponent } from "./scanner-paragraph";
import { ScannerWordComponent } from "./scanner-word";

import { UiServicesModule } from "../services/ui";

const DIRECTIVES = [
	HeaderComponent,
	ScannerParagraphComponent,
	ScannerWordComponent
];

@NgModule({
	imports: [
		CommonModule,
		InteractionDirectivesModule,
		RouterModule,
		UiServicesModule
	],
	declarations: DIRECTIVES,
	exports: DIRECTIVES
})
export class ComponentsModule {

}



