import { Injectable } from "@angular/core";

import { Book, JsonBook } from "../../models";

import { LocalStorageService } from "./local-storage.service";

const BOOK_KEY_PREFIX: string = "book-id-";

const BOOK_ID_LIST_KEY: string = "stored-book-ids";

@Injectable()
export class BookStorageService {
	constructor(private localStorage: LocalStorageService) { }

	getById(id: number | string): JsonBook | undefined {
		const jsonString: string = this.localStorage.get(`${ BOOK_KEY_PREFIX + String(id) }`);
		if (jsonString == null) {
			return;
		}
		return JSON.parse(jsonString) as JsonBook;
	}

	isStored(id: number): boolean {
		return this.getStoredIds().includes(Number(id));
	}

	set(book: JsonBook): void {
		if (this.getById(book.id) != null) {
			return;
		}
		this.localStorage.set(`${ BOOK_KEY_PREFIX + String(book.id) }`, JSON.stringify(book));
		this.addStoredId(book.id);
	}

	clear(): void {
		this.localStorage.clear();
	}

	getStoredIds(): number[] {
		const storedIds = this.localStorage.get(BOOK_ID_LIST_KEY);
		if (storedIds == null) {
			this.localStorage.set(BOOK_ID_LIST_KEY, JSON.stringify([]));
			return [];
		}
		return (JSON.parse(storedIds) as Array<number | string>).map(Number);
	}

	addStoredId(id: number): void {
		id = Number(id);
		const storedIds: number[] = this.getStoredIds();

		if (storedIds.includes(id)) {
			return;
		}

		storedIds.push(id);
		this.localStorage.set(BOOK_ID_LIST_KEY, JSON.stringify(storedIds));
	}
}



