import { Component, HostListener, OnInit } from "@angular/core";

import { JsonBook } from "../../models";

import { BooksApi } from "../../services/api";
import { HeaderService } from "../../services/ui";

@Component({
	templateUrl: "./new-book-page.template.html"
})
export class NewBookPageComponent implements OnInit {
	book: JsonBook = {
		title: "",
		author: "",
		body: ""
	};

	@HostListener("document:keydown", ["$event"])
	handleEnter(event: KeyboardEvent): void {
		if (event.key !== "Enter") {
			return;
		}
		event.preventDefault();
		this.submit();
	}


	constructor(
		private api: BooksApi,
		private headerService: HeaderService
	) {}

	ngOnInit(): void {
		this.setHeaderLink();
	}

	submit(): void {
		this.api.createFromJsonBook(this.book)
			.subscribe((success) => {
					if (success) {
						return;
					}
					console.log("login failed");
				},
				() => {
					console.log("login failed");
				}
			);
	}

	private setHeaderLink(): void {
		this.headerService.link.next({ url: "/books", text: "All Books" });
	}
}



