import { Component, OnDestroy, OnInit } from "@angular/core";

import { Observable, Subscription } from "rxjs";

import * as fromRoot from "../../reducers";
import { Store } from "@ngrx/store";

import * as books from "../../actions/books";
import * as reader from "../../actions/reader";
import * as bookPosition from "../../actions/book-position";

import { BooksApi } from "../../services/api/specialized";
import { Book } from "../../models";

import { HeaderService } from "../../services/ui";

@Component({
	selector: "app-book-list",
	templateUrl: "./book-list-page.template.html"
})
export class BookListPageComponent implements OnInit, OnDestroy {
	books: Book[] = [];

	private booksApiSubscription: Subscription;

	wpm$: Observable<number>;

	constructor(
		private booksApi: BooksApi,
		private store: Store<fromRoot.State>,
		private headerService: HeaderService
	) {
		this.wpm$ = this.store.select(fromRoot.getWpm);
	}

	ngOnInit(): void {
		this.booksApiSubscription = this.booksApi
			.getByFilter()
			.subscribe((books: Book[]) => this.books = books);

		this.setHeaderLink()
	}

	ngOnDestroy(): void {
		this.cancelBooksApiSubscription();
	}

	setWpm(wpm: number): void {
		this.store.dispatch(new reader.WpmAction(wpm));
	}

	get noBooks(): boolean {
		return this.books == null || this.books.length === 0;
	}

	private cancelBooksApiSubscription(): void {
		if (this.booksApiSubscription != null) {
			this.booksApiSubscription.unsubscribe();
			this.booksApiSubscription = null;
		}
	}

	private setHeaderLink(): void {
		this.headerService.link.next({ url: "/books/new", text: "Add Book" });
	}
}



