import { Injectable } from "@angular/core";

import { BehaviorSubject } from "rxjs";

export interface HeaderLink {
	url: string;
	text: string;
}

@Injectable()
export class HeaderService {
	private defaultLink = undefined;

	link: BehaviorSubject<HeaderLink | undefined> = new BehaviorSubject<HeaderLink | undefined>(this.defaultLink);

	setLink(link?: HeaderLink): void {
		this.link.next(link);
	}
}



