import { Injectable } from "@angular/core";

const BUFFER_PADDING: number = 5000,
	MIN_PADDING: number = 10,
	MAX_LOOPS: number = 5;

const withinLoopLimit = (newIdx: number, oldIdx: number): boolean => Math.abs(newIdx - oldIdx) <= MAX_LOOPS;

export interface ContentChildrenBounds {
	firstChildIdx: number;
	lastChildIdx: number;
}

interface ScrollDepthReference {
	element: HTMLElement;
	scrollTop: number;
	offsetTop: number;
}

export interface ParagraphMap {
	[key: string]: HTMLElement;
}

export interface PositionData {
	element: HTMLElement;
	height: number;
	previousSiblingsHeight: number;
}

export interface PositionDataMap {
	[key: string]: PositionData;
}

@Injectable()
export class InfiniteScrollService {

	getChildPositionData(paragraphMap: ParagraphMap): PositionDataMap {
		const holderEl = window.document.createElement("div");
		window.document.body.appendChild(holderEl);

		let previousSiblingsHeight: number = 0;
		const positionDataMap: PositionDataMap = Object.keys(paragraphMap)
			.reduce((map: PositionDataMap, key: string) => {
				// grab reference to next element
				const element: HTMLElement = paragraphMap[key];

				// append to holder div so that height is calculable
				holderEl.appendChild(element);
				const height: number = element.scrollHeight;

				// increment previousSibling height
				previousSiblingsHeight += height;

				map[key] = { element, height, previousSiblingsHeight };
				return map;
			}, {});

		// clean up temp
		holderEl.remove();
		return positionDataMap;
	}

	updatePreviouSiblingHeights(positionDataMap: PositionDataMap): PositionDataMap {
		let previousSiblingsHeight: number = 0;
		return Object.keys(positionDataMap)
			.sort((a, b) => Number(a) - Number(b))
			.reduce((map: PositionDataMap, key: string): PositionDataMap => {
				// create new entry
				map[key] = Object.assign({}, positionDataMap[key], { previousSiblingsHeight });
				// increment previousSiblingHeight
				previousSiblingsHeight += positionDataMap[key].height;
				return map;
			}, {});
	}

	fillContainer(
		contentContainer: HTMLElement,
		scrollContainer: HTMLElement,
		childPositionData: PositionDataMap,
		focusIdx: number = 0,
		focusPaddingTop: number = 0
	): ContentChildrenBounds {
		// start with a clean slate
		this.emptyContainer(contentContainer);

		// put in the focused element first
		const focusChild: HTMLElement = this.getChildByIdx(childPositionData, focusIdx);
		contentContainer.appendChild(focusChild);

		// add enough children before the focused element fill focusPaddingTop
		let firstChildIdx: number = focusIdx;
		while (childPositionData[firstChildIdx - 1] != null && firstChildIdx >= focusIdx - 10) {
			firstChildIdx--;
			contentContainer.insertBefore(
				this.getChildByIdx(childPositionData, firstChildIdx),
				this.getChildByIdx(childPositionData, firstChildIdx + 1)
			);
		}

		// add enough children below fill container
		let lastChildIdx = focusIdx;
		while (childPositionData[lastChildIdx + 1] != null && lastChildIdx <= focusIdx + 10) {
				lastChildIdx++;
				contentContainer.appendChild(this.getChildByIdx(childPositionData, lastChildIdx));
		}

		const topPaddingDiv = window.document.createElement("div");
		topPaddingDiv.style.height = `${ childPositionData[firstChildIdx].previousSiblingsHeight }px`;
		topPaddingDiv.id = "topPaddingDiv";
		contentContainer.insertBefore(topPaddingDiv, this.getChildByIdx(childPositionData, firstChildIdx));

		const bottomPaddingDiv = window.document.createElement("div");
		const bottomPadding = this.getPaddingAfterChild(childPositionData, lastChildIdx);
		bottomPaddingDiv.id = "bottomPaddingDiv";
		bottomPaddingDiv.style.height = `${ bottomPadding }px`;
		contentContainer.appendChild(bottomPaddingDiv);

		// adjust container scroll to match focusPaddingTop
		scrollContainer.scrollTop = focusChild.offsetTop - focusPaddingTop;

		// return the bounds
		return { firstChildIdx, lastChildIdx };
	}

	updateContainerBuffers(
		contentContainer: HTMLElement,
		scrollContainer: HTMLElement,
		childPositionData: PositionDataMap,
		focusIdx: number = 0,
		bounds: ContentChildrenBounds
	): ContentChildrenBounds {
		// get scroll container top & bottom
		const scrollContainerRect: ClientRect = scrollContainer.getBoundingClientRect(),
			scrollContainerTop = scrollContainerRect.top,
			scrollContainerBottom = scrollContainerRect.bottom;

		let newBounds = this.updateBounds(bounds, 0, 0);

		// trim top while necessary
		while (
			withinLoopLimit(bounds.firstChildIdx, newBounds.firstChildIdx) &&
			this.getChildByIdx(childPositionData, newBounds.firstChildIdx).getBoundingClientRect().bottom < BUFFER_PADDING * -1
		) {
			console.log("trimming top");
			newBounds = this.removeFirstChild(childPositionData, newBounds);
			this.updatePaddingDivs(childPositionData, newBounds);
		}

		// trim bottom while necessary
		while (
			withinLoopLimit(bounds.lastChildIdx, newBounds.lastChildIdx) &&
			contentContainer.lastElementChild.getBoundingClientRect().top > scrollContainerBottom + BUFFER_PADDING
		) {
			console.log("trimming bottom");
			newBounds = this.removeLastChild(childPositionData, newBounds);
			this.updatePaddingDivs(childPositionData, newBounds);
		}

		// pad top while necessary
		while (
			childPositionData[newBounds.firstChildIdx - 1] != null &&
			newBounds.firstChildIdx > 0 &&
			this.getChildByIdx(childPositionData, newBounds.firstChildIdx).getBoundingClientRect().top > scrollContainerTop - BUFFER_PADDING
		) {
			console.log("padding top");
			contentContainer.insertBefore(
				this.getChildByIdx(childPositionData, newBounds.firstChildIdx - 1),
				this.getChildByIdx(childPositionData, newBounds.firstChildIdx)
			);
			newBounds = this.updateBounds(newBounds, -1, 0);
			this.updatePaddingDivs(childPositionData, newBounds);
		}

		// pad bottom while necessary
		while (
			childPositionData[newBounds.lastChildIdx + 1] != null &&
			newBounds.lastChildIdx < Object.keys(childPositionData).length - 1 &&
			this.getChildByIdx(childPositionData, newBounds.lastChildIdx).getBoundingClientRect().bottom < scrollContainerBottom + BUFFER_PADDING
		) {
			console.log("padding bottom");
			contentContainer.insertBefore(
				this.getChildByIdx(childPositionData, [newBounds.lastChildIdx + 1]),
				window.document.querySelector("#bottomPaddingDiv") as HTMLElement
			);
			newBounds = this.updateBounds(newBounds, 0, 1);
			this.updatePaddingDivs(childPositionData, newBounds);
		}

		return newBounds;
	}

	updatePaddingDivs(
		childPositionData: PositionDataMap,
		bounds: ContentChildrenBounds,
	): void {
		const topPaddingDiv = window.document.querySelector("#topPaddingDiv") as HTMLElement;
		const bottomPaddingDiv = window.document.querySelector("#bottomPaddingDiv") as HTMLElement;
		topPaddingDiv.style.height = `${ childPositionData[bounds.firstChildIdx].previousSiblingsHeight }px`;
		bottomPaddingDiv.style.height = this.getPaddingAfterChild(childPositionData, bounds.lastChildIdx);
	}

	private emptyContainer(container: HTMLElement): void {
		while (!!container.children.length) {
			container.firstElementChild.remove();
		}
	}

	private removeFirstChild(
		childPositionData: PositionDataMap,
		bounds: ContentChildrenBounds
	): ContentChildrenBounds {
			this.getChildByIdx(childPositionData, bounds.firstChildIdx).remove();
			return this.updateBounds(bounds, 1, 0);
	}

	private removeLastChild(
		childPositionData: PositionDataMap,
		bounds: ContentChildrenBounds
	): ContentChildrenBounds {
			this.getChildByIdx(childPositionData, bounds.lastChildIdx).remove();
			return this.updateBounds(bounds, 0, -1);
	}

	private getPaddingAfterChild(children: PositionDataMap, targetIdx: number): string {
		const targetChild = children[targetIdx],
			lastChild = this.getLastChild(children),
			padding = this.getTotalChildrenHeight(children) - this.getBottomHeight(targetChild);
		return `${ padding }px`;
	}

	private getChildByIdx(children: PositionDataMap, idx): HTMLElement {
		return children[idx].element;
	}

	private updateBounds(current: ContentChildrenBounds, incrementFirst: number, incrementLast: number): ContentChildrenBounds {
		return {
			firstChildIdx: current.firstChildIdx + incrementFirst,
			lastChildIdx: current.lastChildIdx + incrementLast
		};
	}

	private getTotalChildrenHeight(children: PositionDataMap): number {
		const lastChild: PositionData = this.getLastChild(children);
		return lastChild.height + lastChild.previousSiblingsHeight;
	}

	private getLastChild(children: PositionDataMap): PositionData {
		const lastIdx = Math.max(...Object.keys(children).map(Number));
		return children[lastIdx];
	}

	private getBottomHeight(child: PositionData): number {
		return child.height + child.previousSiblingsHeight;
	}
}



