import { Directive, ElementRef, Input, HostListener, Renderer2 } from "@angular/core";

const DEFAULT_CLASS_TIMEOUT: number = 150;

@Directive({
	selector: "[appInteractedClass]"
})
export class InteractedClassDirective {
	@Input()
	appInteractedClass: string;

	@Input()
	classTimeout: number = DEFAULT_CLASS_TIMEOUT;



	@HostListener("mousedown")
	@HostListener("touchstart")
	onInteract(): void {
		this.setInteractedClass();
	}



	constructor(
		private renderer: Renderer2,
		private elementRef: ElementRef
	) { }

	private setInteractedClass(): void {
		if (this.appInteractedClass == null) {
			return;
		}
		this.renderer.addClass(this.elementRef.nativeElement, this.appInteractedClass);
		setTimeout(() => this.removeInteractedClass(), this.classTimeout);
	}

	private removeInteractedClass(): void {
		this.renderer.removeClass(this.elementRef.nativeElement, this.appInteractedClass);
	}
}



