import { Component, HostListener, OnDestroy, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";

import * as fromRoot from "../../reducers";
import { Store } from "@ngrx/store";
import { Observable, Subscription } from "rxjs";

import * as reader from "../../actions/reader";
import * as books from "../../actions/books";
import * as bookPosition from "../../actions/book-position";

import { Book, BookPosition, Paragraph } from "../../models";
import { BookmarksApi, BookPositionService, IntervalService } from "../../services";

@Component({
	templateUrl: "./book-scanner.template.html",
	selector: "app-book-scanner"
})
export class BookScannerComponent implements OnInit, OnDestroy {
	// STORE ITEMS
	play$: Observable<boolean>;

	// ROUTE DATA
	book: Book;
	bookPosition: BookPosition;
	wpm: number;

	private routeParamsSubscription: Subscription;

	@HostListener("click")
	onClick(): void {
		this.togglePlay();
	}

	constructor(
		private route: ActivatedRoute,
		private bookmarksApi: BookmarksApi,
		private bookPositionService: BookPositionService,
		private intervalService: IntervalService,
		private store: Store<fromRoot.State>
	) {
		this.play$ = this.store.select(fromRoot.getPlay);
		this.store.select(fromRoot.getWpm)
			.subscribe((val) => this.wpm = val);
	}

	ngOnInit(): void {
		this.getBookById();
		this.initKeyBindings();
		this.initPlaySubscription();
	}

	ngOnDestroy(): void {
		this.cancelIntervalSubscription();
		this.cancelPlaySubscription();
	}

	togglePlay(): void {
		this.store.dispatch(new reader.TogglePlayAction());
	}

	stepBookPosition(words: number = 1): void {
		this.bookPosition = this.bookPositionService.stepPosition(this.book, this.bookPosition, words);
		this.saveBookmark();
		this.setContent();
	}

	private saveBookmark(): void {
		if (this.bookPosition.wordIdx === 0) {
			this.bookmarksApi
				.create({ ...this.bookPosition, bookId: this.book.id })
				.subscribe((res) => undefined);
		}
	}

	private getBookById(): void {
		this.route.data.subscribe(({ book, bookPosition }) => {
			this.book = book;
			this.bookPosition = bookPosition;
			this.setContent();
		});
	}

	content: Paragraph[];
	private setContent() {
		if (this.content == null) {
			this.initContent();
			return;
		}
		this.appendContent();
	}

	private initContent(): void {
		const contentRangeStart = this.bookPosition.paragraphIdx - 10 > 0 ? this.bookPosition.paragraphIdx - 10  : 0;
		this.content = this.book.content.slice(contentRangeStart, this.bookPosition.paragraphIdx + 100);
	}

	private appendContent(): void {
		const lastParagraphId = this.content[this.content.length - 1].id;
		if (this.bookPosition.paragraphIdx + 20 > this.content[this.content.length - 1].id) {
			const additionalContent = this.book.content.slice(lastParagraphId + 1, lastParagraphId + 101);
			this.content = this.content.concat(additionalContent);
		}
	}

	private initKeyBindings(): void {
		Observable.fromEvent(window.document, "keypress")
			.subscribe((e: KeyboardEvent) => {
				if (e.code !== "Space") {
					return;
				}
				e.preventDefault();
				this.togglePlay();
			});

		Observable.fromEvent(window.document, "keydown")
			.subscribe((e: KeyboardEvent) => {
				if (e.code === "ArrowLeft") {
					this.stepBookPosition(-20);
				}
			});

		Observable.fromEvent(window.document, "keydown")
			.subscribe((e: KeyboardEvent) => {
				if (e.code === "ArrowRight") {
					this.stepBookPosition(20);
				}
			});
	}

	private playSubscription: Subscription;
	private initPlaySubscription(): void {
		this.cancelPlaySubscription();
		this.playSubscription = this.play$
			.subscribe((playing: boolean) => {
				if (playing) {
					this.startRsvInterval();
					return;
				}
				this.cancelIntervalSubscription();
			});
	}

	private cancelPlaySubscription(): void {
		if (this.playSubscription != null) {
			this.playSubscription.unsubscribe();
			this.playSubscription = undefined;
		}
	}

	private intervalSubscription: Subscription;
	private startRsvInterval(): void {
		this.cancelIntervalSubscription();

		this.intervalSubscription = this.intervalService.getIntervalFromWpm(this.wpm)
			.subscribe(() => this.stepBookPosition());
	}

	private cancelIntervalSubscription(): void {
		if (this.intervalSubscription != null) {
			this.intervalSubscription.unsubscribe();
			this.intervalSubscription = undefined;
		}
	}
}



