import { NgModule } from "@angular/core";
import { Route, RouterModule } from "@angular/router";

import { AuthorizedGuard, NotAuthorizedGuard } from "./services/auth";

import { BookPositionResolver, BookResolver } from "./resolvers";

import { BookListPageComponent, BookScannerComponent,
	HomepageComponent, NewBookPageComponent, LoginPageComponent } from "./containers";

const routes: Route[] = [
	{
		path: "books",
		children: [{
				path: "new",
				component: NewBookPageComponent,
			}, {
				path: ":id",
				component: BookScannerComponent,
				resolve: {
					book: BookResolver,
					bookPosition: BookPositionResolver
				}
			}, {
				path: "",
				pathMatch: "full",
				component: BookListPageComponent
			}],
		canActivate: [AuthorizedGuard],
		canActivateChild: [AuthorizedGuard]
	},
	{
		path: "login",
		component: LoginPageComponent,
		canActivate: [NotAuthorizedGuard]
	},
	{
		path: "",
		pathMatch: "full",
		component: HomepageComponent,
		canActivate: [NotAuthorizedGuard]
	},
	{
		path: "**",
		redirectTo: "/books"
	}
];

@NgModule({
	imports: [
		RouterModule.forRoot(
			routes,
			{
				useHash: true,
				enableTracing: false
			}
		)
	],
	providers: [
		BookPositionResolver,
		BookResolver
	],
	exports: [
		RouterModule
	]
})
export class AppRoutingModule { }



