import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";

import { Observable } from "rxjs";

import { BookmarksApi } from "../services/api";

import { Bookmark } from "../models";

@Injectable()
export class BookPositionResolver implements Resolve<Bookmark> {
	constructor(
		private bookmarksApi: BookmarksApi
	) { }

	resolve(
		route: ActivatedRouteSnapshot,
		state: RouterStateSnapshot
	): Observable<Bookmark> {
		const id: string | undefined = route.params.id;
		if (id === "new") {
			return Observable.of();
		} else if (id != null && isNaN(id as any)) {
			// TODO: write a better error message
			return Observable.throw(new Error("Invalid dataflow id"));
		}
		return this.bookmarksApi.getById(Number(id));
	}
}



