import { Action } from "@ngrx/store";

import { PositionDataMap } from "../services/utils/infinite-scroll.service";

export const SET = "[POSITION_DATA] Set";
export const UNSET = "[POSITION_DATA] Unset";

export class SetAction implements Action {
	readonly type = SET;
	constructor(public payload: PositionDataMap) { }
}

export class UnsetAction implements Action {
	readonly type = UNSET;
}

export type Actions =
	SetAction |
	UnsetAction;



