import { createSelector } from "reselect";
import * as positionData from "../actions/position-data";

import { PositionDataMap } from "../services/utils/infinite-scroll.service";

export interface State {
	positionData?: PositionDataMap;
}

export const initialState: State = {};

export function reducer(state: State = initialState, action: positionData.Actions): State {
	switch (action.type) {
		case positionData.SET:
			return Object.assign(
				{},
				state,
				{ positionData: action.payload }
			);
		case positionData.UNSET:
			return Object.assign(
				{},
				state,
				{ positionData: undefined }
			);
		default:
			return state;
	}
}

export const getPositionData = (state: State): PositionDataMap => state.positionData;



